#home_container {
      width: 100vw;
      height: 100vh;
      display: flex;
      text-align: right;
      justify-content: flex-end;
      align-content: right;
      align-items: center;
}


.header_title {
      color: #CCDDCE;
      font-weight: 300;
      font-stretch: normal;
      font-size: 16px;
      line-height: 1.5;
      font-weight: 300;
      color: #efdab9;
      height: auto;
      padding: 10% 10% 40% 5%;
      margin: 16px;
}

#home_background_animation {
      position: absolute;
      width: 100vw;
      height: 100vh;
      z-index: -1;
}
